body {
  background-color:rgb(71, 70, 70)!important;
}

nav {
  position: fixed;
  top: 5px;
  left: 0;
  width: 100%;
}

.nav-ul {
  background-color: black;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 3px;
}

.nav-ul li {
  list-style: none;
  margin: 10px;
}

.nav-ul li a {
  color: white;
  font-size: 20px;
  text-decoration: none;
  padding: 10px;
  background-color: black;
  border: 2px solid red;
}

.nav-ul li a:hover {
  border: 2px solid whitesmoke;
  border-radius: 8px;
}

.ul-pr-shopping {
  background-color: brown;
  padding: 10px;
  margin: 10px;
  border: 1px solid wheat;
  border-radius: 3px;
}

.ul-pr-selcted {
  background-color: brown;
  padding: 10px;
  margin: 10px;
  border: 1px solid wheat;
  border-radius: 3px;
}

.ul-pr-selcted li {
  list-style: none;
  margin: 10px;
  font-size: 25px;
  color: blanchedalmond;
  font-weight: 500;
}

.ul-pr-shopping li {
  list-style: none;
  margin: 10px;
  font-size: 25px;
  color: blanchedalmond;
  font-weight: 500;

}

.h1-ch {
  text-align: center;
  color: rgb(66, 51, 51);
  background-color: hsl(39, 77%, 83%, 0.6);
  padding: 10px;
  text-shadow: 1px 1px 2px wheat;
  margin-top: 8%;
}

.add-btn {
  padding: 10px;
  background-color: black;
  color: beige;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.add-btn:hover {
  background-color: rgb(255, 112, 112);
  color: white;
}

.sel-h1 {
  text-align: center;
  color: dodgerblue;
  text-shadow: 1px 1px 2px black;
  margin-top: 7%;
}

.add-self {
  padding: 10px;
  font-size: 20px;
  background-color: dodgerblue;
  border: none;
  cursor: pointer;
  font-weight: 800;
}

.add-self:hover {
  /* border-radius: 5px; */
  /* border: 1px white solid; */
  background-color: teal;
  color: wheat;
}

img {
  max-width: 250px;
  border: 1px solid wheat;
  border-radius: 2px;
  box-shadow: 2px 2px 1px whitesmoke,
              -2px -2px 1px white;
}

.ul-pr-selcted .del {
  padding: 10px;
  background-color: black;
  border: none;
  font-size: 20px;
  color: orange;
  cursor: pointer;
}

.ul-pr-shopping h2 {
  background-color: rgb(73, 75, 80);
  width: 300px;
  padding: 10px;
  border-radius: 8px;
  color: palegoldenrod;
  margin-left: 7px;
}

.cost {
  background-color: teal;
  color: white;
  padding: 10px;
  width: 300px;
  border-radius: 4px;
  text-align: center;
  line-height: 60px;
}

.order {
  background-color: teal;
  color: white;
  padding: 10px;
  width: 800px;
  border-radius: 6px;
  text-decoration: none;
}

.cost-order-div {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-top: 3%;
}

.cost-order-div h2 {
  margin: 10px;
}

.order a {
  color: white!important;
  font-size: 20px;
  text-decoration: none;
  padding: 10px;
  background-color: black;
  border: 3px solid white;
  cursor: pointer;
  border-radius: 5px;
  margin: 5px;
}

.order {
  line-height: 60px;
}

.order a:hover {
  background-color: brown;
  border: 3px solid white;
}

.incdecbtns {
  display: flex;
  justify-content: flex-start;
}

.incdecbtns li {
  margin: 10px;
}

.img-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.details {
 max-width: 800px;
  /* background-color: hsl(180, 100%, 25%, 0.5); */
  border: 1px solid wheat;
  padding: 10px;
  color: palegoldenrod;
}
/* .img-details  */

.log-h1 {
  margin-top: 10%;
  text-align: center;
  color: wheat;
}

.form {
  max-width: 600px;
  border: 2px solid rgb(161, 158, 158);
  padding: 10px;
  margin: 5px auto;
  background-color: teal;
  color: wheat;
  font-size: 20px;
  border-radius: 7px;
  font-size: bold;
    
}

.form input {
  font-size: 20px;
}

.sub-done {
  width: 300px;
  background-color: teal;
  padding: 10px;
  margin: 10px;
  font-size: 20px;
  color: white;
}